import { IKey } from '@activeledger/sdk';
import { PersistenceService, StorageType } from 'angular-persistence';
import { Injectable } from '@angular/core';


@Injectable({
    providedIn: 'root'
})

export class LedgerHelper {
  
    constructor(private persistenceService: PersistenceService) { 
    }

    loginUrl = 'http://testnet-uk.activeledger.io:5555/login';
    registerUrl = 'http://testnet-uk.activeledger.io:5555/register';
    createProfileUrl = 'http://testnet-uk.activeledger.io:5555/transaction/createProfile';
    addReportURL = 'http://testnet-uk.activeledger.io:5555/transaction';
    updateProfileUrl = 'http://testnet-uk.activeledger.io:5555/transaction';
    updateReportURL = 'http://testnet-uk.activeledger.io:5555/transaction';



    get key(): IKey {
        return JSON.parse(localStorage.getItem('key:' + this.email)) as IKey;
    }
    set key(key: IKey) {
        localStorage.setItem('key:' + this.email, JSON.stringify(key));
    }

    sanitizeDP(dp: string): string{
        const to_remove = 'data:image/jpeg;base64,';
        const to_remove2 = 'SafeValue must use [property]=binding:';
        const to_remove3 = ' (see http://g.co/ng/security#xss)';
        return dp.replace(to_remove, '').replace(to_remove2, '').replace(to_remove3, '');
    }

   get token(): string {
        return localStorage.getItem('Token');
    }
     set token(token: string) {
        localStorage.setItem('Token', token);
    }

    get address(): string {
        return localStorage.getItem('address');
    }
    set address(address: string) {
        localStorage.setItem('address', address);
    }

    get date_of_birth(): string {
        return localStorage.getItem('date_of_birth');
    }
    set date_of_birth(date_of_birth: string) {
        localStorage.setItem('date_of_birth', date_of_birth);
    }

    get dp(): string {
        return this.sanitizeDP(localStorage.getItem('dp'));
    }
    set dp(dp: string) {
        localStorage.setItem('dp', this.sanitizeDP(dp));
    }

    get email(): string {
        return localStorage.getItem('email');
    }
    set email(email: string) {
        localStorage.setItem('email', email);
    }

    get first_name(): string {
        return localStorage.getItem('first_name');
    }
    set first_name(first_name: string) {
        localStorage.setItem('first_name', first_name);
    }

    get last_name(): string {
        return localStorage.getItem('last_name');
    }
    set last_name(last_name: string) {
        localStorage.setItem('last_name', last_name);
    }

    get full_name(): string{
        return this.first_name + ' ' + this.last_name;
    }

    get gender(): string {
        return localStorage.getItem('gender');
    }
    set gender(gender: string) {
        localStorage.setItem('gender', gender);
    }

    get phone_number(): string {
        return localStorage.getItem('phone_number');
    }
    set phone_number(phone_number: string) {
        localStorage.setItem('phone_number', phone_number);
    }

    get profile_type(): string {
        return localStorage.getItem('profile_type');
    }
    set profile_type(profile_type: string) {
        localStorage.setItem('profile_type', profile_type);
    }

    get resecurityports(): string {
        return localStorage.getItem('security');
    }
    set security(security: string) {
        localStorage.setItem('security', security);
    }

    get _id(): string {
        return localStorage.getItem('_id');
    }
    set _id(_id: string) {
        localStorage.setItem('_id', _id);
    }

    get profileCreated(): string {
        return localStorage.getItem('profileCreated');
    }
    set profileCreated(profileCreated: string) {
        localStorage.setItem('profileCreated', profileCreated);
    }

    get isLoggedin(): string {
        return localStorage.getItem('isLoggedin');
    }
    set isLoggedin(isLoggedin: string) {
        localStorage.setItem('isLoggedin', isLoggedin);
    }

    get report(): string {
        return localStorage.getItem('report');
    }
    set report(report: string) {
        localStorage.setItem('report', report);
    }

    get reports(): string {
        // return this.persistenceService.get('reportlist', StorageType.SESSION);
        return this.persistenceService.get('reportlist');
    }
    set reports(reports: string) {
        this.persistenceService.set('reportlist', reports);
        // this.persistenceService.set('reportlist', reports, { type: StorageType.SESSION });

    }

    get userList(): string {
        return this.persistenceService.get('userList');
        // return this.persistenceService.get('userList', StorageType.SESSION);

    }
    set userList(users: string) {
        this.persistenceService.set('userList', users);
        // this.persistenceService.set('userList', users, { type: StorageType.SESSION }) ;
    }


    // tslint:disable-next-line: max-line-length
    placeholder_img = '';

}
